import { FC } from "react";
import { Link } from "react-router-dom";
import { Typography, List, Avatar, Divider } from "antd";

import { Listing as ListingData } from "lib/graphql/queries";

interface Props {
  limit: number;

  bookingsPage: number;
  setBookingsPage: (page: number) => void;
  listingBookings: ListingData["listing"]["bookings"];
}

const { Title, Text } = Typography;

export const ListingBookings: FC<Props> = ({
  limit,
  bookingsPage,
  listingBookings,
  setBookingsPage,
}) => {
  const total = listingBookings?.total || null;
  const result = listingBookings?.result || null;

  const listingBookingsList = listingBookings ? (
    <List
      dataSource={result || undefined}
      grid={{ gutter: 8, xs: 1, sm: 2, lg: 3 }}
      locale={{ emptyText: "No bookings have been made yet!" }}
      pagination={{
        showLessItems: true,
        current: bookingsPage,
        defaultPageSize: limit,
        hideOnSinglePage: true,
        total: total || undefined,
        onChange: (page: number) => setBookingsPage(page),
      }}
      renderItem={(booking) => {
        const bookingHistory = (
          <div className="listing-bookings__history">
            <div>
              Check in: <Text strong>{booking.checkIn}</Text>
            </div>
            <div>
              Check out: <Text strong>{booking.checkOut}</Text>
            </div>
          </div>
        );

        return (
          <List.Item className="listing-bookings__item">
            {bookingHistory}
            <Link to={`/user/${booking.tenant.id}`}>
              <Avatar
                size={64}
                shape="square"
                src={`${booking.tenant.avatar}`}
              />
            </Link>
          </List.Item>
        );
      }}
    />
  ) : null;

  return listingBookingsList ? (
    <div className="listing-bookings">
      <Divider />
      <div className="listing-bookings__section">
        <Title level={4}>Bookings</Title>
      </div>

      {listingBookingsList}
    </div>
  ) : null;
};
