import { FC, useState } from "react";
import {
  Form,
  Radio,
  Input,
  Layout,
  Upload,
  Button,
  Typography,
  InputNumber,
} from "antd";
import {
  HomeOutlined,
  BankOutlined,
  PlusOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { useMutation } from "@apollo/client";
import { Link, Redirect } from "react-router-dom";
import { UploadChangeParam } from "antd/lib/upload";

import {
  iconColor,
  displayErrorMessage,
  displaySuccessNotification,
} from "lib/utils";
import {
  HOST_LISTING,
  HostListingVariables,
  HostListing as HostListingData,
} from "lib/graphql/mutations/HostListing";
import { Viewer } from "lib/types";
import { ListingType } from "lib/graphql/globalTypes";

interface Props {
  viewer: Viewer;
}

interface FormValues {
  zip: string;
  city: string;
  state: string;
  image: string;
  title: string;
  price: number;
  guests: number;
  address: string;
  type: ListingType;
  description: string;
}

type MutationInput = HostListingVariables["input"];

const { Item } = Form;
const { Content } = Layout;
const { Title, Text } = Typography;
const { APARTMENT, HOUSE } = ListingType;

export const Host: FC<Props> = ({ viewer }) => {
  const [imageLoading, setImageLoading] = useState(false);
  const [imageBase64Value, setImageBase64Value] = useState<string | null>(null);
  const [hostListing, { loading, data }] = useMutation<
    HostListingData,
    HostListingVariables
  >(HOST_LISTING, {
    onCompleted: () => {
      displaySuccessNotification("You've successfully created your listing!");
    },
    onError: () => {
      displayErrorMessage(
        "Sorry! We weren't able to create your listing. Please try again later."
      );
    },
  });

  const handleImageUpload = (info: UploadChangeParam) => {
    const { file } = info;

    if (file.status === "uploading") {
      setImageLoading(true);
      return;
    }

    if (file.status === "done" && file.originFileObj) {
      getBase64Value(file.originFileObj, (imageBase64Value) => {
        setImageBase64Value(imageBase64Value);
        setImageLoading(false);
      });
    }
  };

  const handleHostListing = (values: FormValues) => {
    const {
      zip,
      city,
      type,
      price,
      state,
      title,
      guests,
      address,
      description,
    } = values;
    const fullAddress = `${address}, ${city}, ${state}, ${zip}`;

    const input: MutationInput = {
      type,
      title,
      description,
      price: price * 100,
      numOfGuests: guests,
      address: fullAddress,
      image: imageBase64Value || "",
    };

    hostListing({ variables: { input } });
  };

  if (!viewer.id || !viewer.hasWallet) {
    return (
      <Content className="host-content">
        <div className="host__form-header">
          <Title level={4} className="host__form-title">
            You'll have to be signed in and connected with Stripe to host a
            listing!
          </Title>
          <Text type="secondary">
            We only allow users who've signed in to our application and have
            connected with Stripe to host new listings. You can sign in at the{" "}
            <Link to="/login">/login</Link> page and connect with Stripe shortly
            after.
          </Text>
        </div>
      </Content>
    );
  }

  if (loading) {
    return (
      <Content className="host-content">
        <div className="host__form-header">
          <Title level={4} className="host__form-title">
            Please wait!
          </Title>
          <Text type="secondary">We're creating your listing now.</Text>
        </div>
      </Content>
    );
  }

  if (data && data.hostListing.id) {
    return <Redirect to={`/listing/${data.hostListing.id}`} />;
  }

  return (
    <Content className="host-content">
      <Form layout="vertical" onFinish={handleHostListing}>
        <div className="host__form-header">
          <Title level={3} className="host_form-title">
            Hi! Let's get started listing your place.
          </Title>
          <Text type="secondary">
            In this form, we'll collect some basic and additional information
            about your listing.
          </Text>
        </div>

        <Item
          name="type"
          label="Home Type"
          rules={[
            { required: true, message: "Please select a home type" },
            {
              enum: [APARTMENT, HOUSE],
              message: "You can select only home or apartment type",
            },
          ]}
        >
          <Radio.Group>
            <Radio.Button value={APARTMENT}>
              <BankOutlined color={iconColor} />
              <span>Apartment</span>
            </Radio.Button>
            <Radio.Button value={HOUSE}>
              <HomeOutlined color={iconColor} />
              <span>House</span>
            </Radio.Button>
          </Radio.Group>
        </Item>

        <Item
          name="guests"
          label="Max # of Guests"
          rules={[
            {
              required: true,
              message: "Please enter the max number of quests",
            },
          ]}
        >
          <InputNumber min={1} placeholder="4" />
        </Item>

        <Item
          name="title"
          label="Title"
          extra="Max character count of 45"
          rules={[
            {
              required: true,
              message: "Please select a title for your listing",
            },
          ]}
        >
          <Input
            maxLength={45}
            placeholder="The iconic and luxurious Bel-Air mansion"
          />
        </Item>

        <Item
          name="description"
          label="Description of listing"
          extra="Max character count of 400"
          rules={[
            {
              required: true,
              message: "Please enter a description for your listing",
            },
          ]}
        >
          <Input.TextArea
            rows={3}
            maxLength={400}
            placeholder="Modern, clean, and iconic home of the Fresh Prince.Situated in the heart of Bel-Air, Los Angeles."
          />
        </Item>

        <Item
          name="address"
          label="Address"
          rules={[
            {
              required: true,
              message: "Please enter a address for your listing",
            },
          ]}
        >
          <Input placeholder="251 North Briston Avenue" />
        </Item>

        <Item
          name="city"
          label="City/Town"
          rules={[
            {
              required: true,
              message: "Please enter a city name for your listing",
            },
          ]}
        >
          <Input placeholder="Los Angeles" />
        </Item>

        <Item
          name="state"
          label="State/Province"
          rules={[
            {
              required: true,
              message: "Please enter a state name for your listing",
            },
          ]}
        >
          <Input placeholder="California" />
        </Item>

        <Item
          name="zip"
          label="Zip/Postal code"
          rules={[
            {
              required: true,
              message: "Please enter a postal code for your listing",
            },
          ]}
        >
          <Input placeholder="Please enter a zip code for your listing!" />
        </Item>

        <Item
          name="image"
          label="Image"
          extra="Images must be under 1MB in size and of type JPG or PNG"
          rules={[
            {
              required: true,
              message: "Please provide a image for your listing",
            },
          ]}
        >
          <div className="host__form-image-upload">
            <Upload
              name="image"
              showUploadList={false}
              listType="picture-card"
              onChange={handleImageUpload}
              beforeUpload={beforeImageUpload}
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
            >
              {imageBase64Value ? (
                <img alt="Listing" src={imageBase64Value} />
              ) : (
                <div>
                  {imageLoading ? <LoadingOutlined /> : <PlusOutlined />}
                  <div className="and-upload-text">Upload</div>
                </div>
              )}
            </Upload>
          </div>
        </Item>

        <Item
          name="price"
          label="Price"
          extra="All prices in $USD/day"
          rules={[
            {
              required: true,
              message: "Please enter a price for your listing",
            },
          ]}
        >
          <InputNumber placeholder="120" min={0} />
        </Item>

        <Item>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </Item>
      </Form>
    </Content>
  );
};

const beforeImageUpload = (file: File): boolean => {
  const fileIsValidType =
    file.type === "image/jpeg" || file.type === "image/png";
  const fileIsValidSize = file.size / 1024 / 1024 < 1;

  if (!fileIsValidSize) {
    displayErrorMessage(
      "You are only able to upload valid image files of under 1MB in size!"
    );
    return false;
  }

  if (!fileIsValidType) {
    displayErrorMessage("You are only able to upload valid JPG or PNG files!");
    return false;
  }

  return fileIsValidSize && fileIsValidType;
};

const getBase64Value = (
  img: File | Blob,
  callback: (imageBase64Value: string) => void
) => {
  const reader = new FileReader();
  reader.readAsDataURL(img);
  reader.onload = () => {
    callback(reader.result as string);
  };
};
