import { Layout, Input } from "antd";
import { FC, useState, ChangeEvent, useEffect } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";

import { Viewer } from "lib/types";
import { MenuItems } from "./components";
import { displayErrorMessage } from "lib/utils";
import logo from "./assets/tinyhouse-logo.png";

interface Props {
  viewer: Viewer;
  setViewer: (viewer: Viewer) => void;
}

const { Search } = Input;
const { Header } = Layout;

export const AppHeader: FC<Props> = ({ viewer, setViewer }) => {
  const { push } = useHistory();
  const { pathname } = useLocation();
  const [search, setSearch] = useState("");

  useEffect(() => {
    const pathnameSubStrings = pathname.split("/");
    const pathIncludes = pathname.includes("/listings");
    if (!pathIncludes) {
      setSearch("");
    }

    if (pathIncludes && pathnameSubStrings.length === 3) {
      setSearch(pathnameSubStrings[2]);
      return;
    }
  }, [pathname]);

  const onSearch = (value: string) => {
    const trimmedValue = value.trim();
    if (trimmedValue) {
      push(`/listings/${trimmedValue}`);
    } else {
      displayErrorMessage("Please enter a valid search value!");
    }
  };

  const onChange = ({ target }: ChangeEvent<HTMLInputElement>) =>
    setSearch(target.value);

  return (
    <Header className="app-header">
      <div className="app-header__logo-search-section">
        <div className="app-header__logo">
          <Link to="/">
            <img src={logo} alt="App Logo" />
          </Link>
        </div>
        <div className="app-header__search-input">
          <Search
            enterButton
            value={search}
            onChange={onChange}
            onSearch={onSearch}
            placeholder="Search 'San Francisco'"
          />
        </div>
      </div>
      <div className="app-header__menu-section">
        <MenuItems viewer={viewer} setViewer={setViewer} />
      </div>
    </Header>
  );
};
