import { FC } from "react";
import {
  CardElement,
  injectStripe,
  ReactStripeElements,
} from "react-stripe-elements";
import moment, { Moment } from "moment";
import { useMutation } from "@apollo/client";
import { KeyOutlined } from "@ant-design/icons";
import { Modal, Typography, Button, Divider } from "antd";

import {
  CREATE_BOOKING,
  CreateBookingVariables,
  CreateBooking as CreateBookingData,
} from "lib/graphql/mutations/CreateBooking";
import { formatListingPrice } from "lib/utils";
import { displayErrorMessage, displaySuccessNotification } from "lib/utils";

interface Props {
  id: string;
  price: number;
  checkInDate: Moment;
  checkOutDate: Moment;
  modalVisible: boolean;
  clearBookingData(): void;
  handleListingRefetch(): Promise<void>;
  setModalVisible(modalVisible: boolean): void;
}

const { Paragraph, Text, Title } = Typography;

const ListingCreateBookingModalElement: FC<
  Props & ReactStripeElements.InjectedStripeProps
> = ({
  id,
  price,
  stripe,
  checkInDate,
  checkOutDate,
  modalVisible,
  setModalVisible,
  clearBookingData,
  handleListingRefetch,
}) => {
  const onCancel = () => setModalVisible(false);
  const [createBooking, { loading }] = useMutation<
    CreateBookingData,
    CreateBookingVariables
  >(CREATE_BOOKING, {
    onCompleted: () => {
      clearBookingData();
      displaySuccessNotification(
        "You've successfully booked the listing!",
        "Booking history can always be found in your User page."
      );
      handleListingRefetch();
    },
    onError: () => {
      displayErrorMessage(
        "Sorry! We weren't able to successfully book the listing. Please ty again later!"
      );
    },
  });

  const daysBooked = checkOutDate.diff(checkInDate, "days") + 1;
  const listingPrice = price * daysBooked;

  const handleCreateBooking = async () => {
    if (!stripe) {
      return displayErrorMessage(
        "Sorry! We were not abble to connect with Stripe!"
      );
    }

    let { token: stripeToken, error } = await stripe.createToken();
    if (stripeToken) {
      createBooking({
        variables: {
          input: {
            id,
            source: stripeToken.id,
            checkIn: moment(checkInDate).format("YYYY-MM-DD"),
            checkOut: moment(checkOutDate).format("YYYY-MM-DD"),
          },
        },
      });
    } else {
      displayErrorMessage(
        error && error.message
          ? error.message
          : "Sorry! We weren't able to book the listing. Please try again later!"
      );
    }
  };

  return (
    <Modal centered footer={null} onCancel={onCancel} visible={modalVisible}>
      <div className="listing-booking-modal">
        <div className="listing-booking-modal__intro">
          <Title className="listing-booking-modal__intro-title">
            <KeyOutlined />
          </Title>
          <Title level={3} className="listing-booking-modal__intro-title">
            Book your trip
          </Title>
          <Paragraph>
            Enter your payment information to book the listing from the dates
            between
            <Text mark strong>
              {moment(checkInDate).format("MMMM Do YYYY")}
            </Text>{" "}
            and{" "}
            <Text mark strong>
              {moment(checkOutDate).format("MMMM Do YYYY")}
            </Text>
            , inclusive.
          </Paragraph>
        </div>
        <Divider />
        <div className="listing-booking-modal__charge-summary">
          <Paragraph>
            {formatListingPrice(price, false)} * {daysBooked} days ={" "}
            <Text strong>{formatListingPrice(listingPrice, false)}</Text>
          </Paragraph>
          <Paragraph className="listing-booking-modal__charge-summary-total">
            Total = <Text mark>{formatListingPrice(listingPrice, false)}</Text>
          </Paragraph>
        </div>
        <Divider />
        <div className="listing-booking-modal__stripe-card-section">
          <div style={{ padding: "20px 0" }}>
            <CardElement hidePostalCode />
          </div>
          <Button
            size="large"
            type="primary"
            loading={loading}
            onClick={handleCreateBooking}
            className="listing-booking-modal__cta"
          >
            Book
          </Button>
        </div>
      </div>
    </Modal>
  );
};

export const ListingCreateBookingModal = injectStripe(
  ListingCreateBookingModalElement
);
