import { FC } from "react";
import moment, { Moment } from "moment";
import { Card, Button, Divider, Typography, DatePicker } from "antd";

import { Viewer } from "lib/types";
import { BookingsIndex } from "./types";
import { Listing as ListingData } from "lib/graphql/queries";
import { formatListingPrice, displayErrorMessage } from "lib/utils";

interface Props {
  price: number;
  viewer: Viewer;
  checkInDate: Moment | null;
  checkOutDate: Moment | null;
  host: ListingData["listing"]["host"];
  setModalVisible(modalVisible: boolean): void;
  setCheckInDate: (date: Moment | null) => void;
  setCheckOutDate: (date: Moment | null) => void;
  bookingsIndex: ListingData["listing"]["bookingsIndex"];
}

const { Paragraph, Title, Text } = Typography;

export const ListingCreateBooking: FC<Props> = ({
  host,
  price,
  viewer,
  checkInDate,
  checkOutDate,
  bookingsIndex,
  setCheckInDate,
  setModalVisible,
  setCheckOutDate,
}) => {
  const bookingsIndexJSON = JSON.parse(bookingsIndex) as BookingsIndex;

  const dateIsBooked = (currentDate: Moment): boolean => {
    const day = moment(currentDate).day();
    const year = moment(currentDate).year();
    const month = moment(currentDate).month();

    if (bookingsIndexJSON[year] && bookingsIndexJSON[year][month]) {
      return Boolean(bookingsIndexJSON[year][month][day]);
    } else {
      return false;
    }
  };
  const disabledDate = (currentDate?: Moment) => {
    if (currentDate) {
      const dateIsBeforeEndOfDay = currentDate.isBefore(moment().endOf("day"));
      return dateIsBeforeEndOfDay || dateIsBooked(currentDate);
    } else {
      return false;
    }
  };
  const verifyAndSetCheckOutDate = (selectedCheckOutDate: Moment | null) => {
    if (checkInDate && selectedCheckOutDate) {
      if (moment(selectedCheckOutDate).isBefore(checkInDate, "days")) {
        return displayErrorMessage(
          "You can not book date of check out to be prior to check in!"
        );
      }
    }

    let dateCursor = checkInDate;
    while (moment(dateCursor).isBefore(selectedCheckOutDate, "days")) {
      dateCursor = moment(dateCursor).add(1, "days");

      const day = moment(dateCursor).day();
      const year = moment(dateCursor).year();
      const month = moment(dateCursor).month();

      if (
        bookingsIndexJSON[year] &&
        bookingsIndexJSON[year][month] &&
        bookingsIndexJSON[year][month][day]
      ) {
        return displayErrorMessage(
          "You can not book a period of time that overlaps existing bookings. Please try again!"
        );
      }
    }
    setCheckOutDate(selectedCheckOutDate);
  };

  const viewerIsHost = viewer.id === host.id;
  const checkInInputDisabled = !viewer.id || viewerIsHost || !host.hasWallet;
  const checkOutInputDisabled = checkInInputDisabled || !checkInDate;
  const buttonDisabled = checkInInputDisabled || !checkInDate || !checkOutDate;

  const resetCheckOutDate = () => setCheckOutDate(null);

  let buttonMessage = "You won't be charget yet!";
  if (checkInInputDisabled) {
    buttonMessage = "You have to be signed in to book a listing!";
  }
  if (viewerIsHost) {
    buttonMessage = "You can not book your own listing!";
  }
  if (!host.hasWallet) {
    buttonMessage =
      "The host has disconnected from Stripe and thus won't receive payments!";
  }

  const buttonOnClickHandler = () => setModalVisible(true);

  return (
    <div className="listing-booking">
      <Card className="listing-booking__card">
        <div>
          <Paragraph>
            <Title level={2} className="listing-booking__card-title">
              {formatListingPrice(price)}
              <span>/day</span>
            </Title>
          </Paragraph>
          <Divider />
          <div className="listing-booking__card-date-picker">
            <Paragraph strong>Check In</Paragraph>
            <DatePicker
              showToday={false}
              format="YYYY/MM/DD"
              value={checkInDate}
              onChange={setCheckInDate}
              disabledDate={disabledDate}
              disabled={checkInInputDisabled}
              onOpenChange={resetCheckOutDate}
            />
          </div>
          <div className="listing-booking__card-date-picker">
            <Paragraph strong>Check Out</Paragraph>
            <DatePicker
              showToday={false}
              format="YYYY/MM/DD"
              value={checkOutDate}
              disabledDate={disabledDate}
              disabled={checkOutInputDisabled}
              onChange={verifyAndSetCheckOutDate}
            />
          </div>
        </div>
        <Divider />
        <Button
          size="large"
          type="primary"
          disabled={buttonDisabled}
          onClick={buttonOnClickHandler}
          className="listing-booking__card-cta"
        >
          Request to book!
        </Button>
        <Text type="secondary" mark>
          {buttonMessage}
        </Text>
      </Card>
    </div>
  );
};
