import { gql } from "@apollo/client";

export const LISTINGS = gql`
  query Listings(
    $page: Int!
    $limit: Int!
    $location: String
    $filter: ListingFilter!
  ) {
    listings(limit: $limit, page: $page, filter: $filter, location: $location) {
      total
      region
      result {
        id
        title
        image
        price
        address
        numOfGuests
      }
    }
  }
`;

export * from "./__generated__/Listings";
