import { FC, Fragment } from "react";
import { useMutation } from "@apollo/client";
import { Avatar, Card, Typography, Divider, Button, Tag } from "antd";

import {
  formatListingPrice,
  displayErrorMessage,
  displaySuccessNotification,
} from "lib/utils";
import { Viewer } from "lib/types";
import {
  DISCONNECT_STRIPE,
  DisconnectStripe as DisconnectStripeData,
} from "lib/graphql/mutations/DisconnectStripe";
import { User as UserData } from "lib/graphql/queries";

interface Props {
  viewer: Viewer;
  viewerIsUser: boolean;
  user: UserData["user"];
  handleUserRefetch(): Promise<void>;
  setViewer: (viewer: Viewer) => void;
}

const {
  REACT_APP_S_OAUTH_URL: authUrl,
  REACT_APP_S_CLIENT_ID: clientId,
} = process.env;
const stripeAuthUrl = authUrl.replace("{{}}", clientId);

const { Paragraph, Text, Title } = Typography;
export const UserProfile: FC<Props> = ({
  user,
  viewer,
  setViewer,
  viewerIsUser,
  handleUserRefetch,
}) => {
  const [disconnectStripe, { loading }] = useMutation<DisconnectStripeData>(
    DISCONNECT_STRIPE,
    {
      onCompleted: (data) => {
        setViewer({ ...viewer, hasWallet: data.disconnectStripe.hasWallet });
        displaySuccessNotification(
          "You've successfully disconnected from Stripe!",
          "Yoy'll have to reconnect with Stripe to continue to create listings."
        );
        handleUserRefetch();
      },
      onError: () => {
        displayErrorMessage(
          "Sorry! We weren't able to disconnect you from Stripe. Please try again later!"
        );
      },
    }
  );

  const disconnect = () => disconnectStripe();

  const redirectToStripe = () => (window.location.href = stripeAuthUrl);

  const additionalDetails = user.hasWallet ? (
    <Fragment>
      <Paragraph>
        <Tag color="green">Stripe Registered</Tag>
      </Paragraph>
      <Paragraph>
        Income Earned:{" "}
        <Text strong>
          {user.income ? formatListingPrice(user.income) : "0$"}
        </Text>
      </Paragraph>
      <Button
        type="primary"
        loading={loading}
        disabled={loading}
        onClick={disconnect}
        className="user-profile__details-cta"
      >
        Disconnect Stripe
      </Button>
      <Paragraph type="secondary">
        By disconnecting, you won't be able to receive{" "}
        <Text strong>any further payments</Text>. This will prevent users from
        booking listings that you might have already created.
      </Paragraph>
    </Fragment>
  ) : (
    <Fragment>
      <Paragraph>
        Interested in becoming a TinyHouse host? Register with your Stripe
        account!
      </Paragraph>
      <Button
        type="primary"
        className="user-profile__details-cta"
        onClick={redirectToStripe}
      >
        Connect with Stripe
      </Button>
      <Paragraph type="secondary">
        TinyHouse uses{" "}
        <a
          target="_blank"
          rel="noopener noreferrer"
          href="https://stripe.com/en-US/connect"
        >
          Stripe
        </a>{" "}
        to help transfer your earnings in a secure and truster manner.
      </Paragraph>
    </Fragment>
  );

  const additionalDetailsSection = viewerIsUser ? (
    <>
      <Divider />
      <div className="user-profile__details">
        <Title level={4}>Additional Details</Title>
        {additionalDetails}
      </div>
    </>
  ) : null;
  return (
    <div className="user-profile">
      <Card className="user-profile__card">
        <div className="user-profile__avatar">
          <Avatar size={100} src={user.avatar} />
        </div>
        <Divider />
        <div className="user-profile__details">
          <Title level={4}>Details</Title>
          <Paragraph>
            Name: <Text strong>{user.name}</Text>
          </Paragraph>
          <Paragraph>
            Contact: <Text strong>{user.contact}</Text>
          </Paragraph>
        </div>
        {additionalDetailsSection}
      </Card>
    </div>
  );
};
