import { Layout, Spin } from "antd";
import { useMutation } from "@apollo/client";
import { FC, useEffect, useRef, useMemo } from "react";
import { Redirect, useHistory } from "react-router-dom";

import {
  CONNECT_STRIPE,
  ConnectStripeVariables,
  ConnectStripe as ConnectStripeData,
} from "lib/graphql/mutations/ConnectStripe";
import { Viewer } from "lib/types";
import { displaySuccessNotification } from "lib/utils";

interface Props {
  viewer: Viewer;
  setViewer(viewer: Viewer | ((v: Viewer) => Viewer)): void;
}

const { Content } = Layout;

export const Stripe: FC<Props> = ({ viewer, setViewer }) => {
  const { replace } = useHistory();
  const [connectStripe, { data, loading, error }] = useMutation<
    ConnectStripeData,
    ConnectStripeVariables
  >(CONNECT_STRIPE, {
    onCompleted: ({ connectStripe: { hasWallet } }) => {
      setViewer((v) => ({ ...v, hasWallet }));
      displaySuccessNotification(
        "You've successfully connected your Stripe Account!",
        "You can now begin to create listings in the Host page."
      );
    },
  });

  const viewerMemo = useMemo(() => viewer, [viewer]);

  const connectStripeRef = useRef(connectStripe);

  useEffect(() => {
    const code = new URL(window.location.href).searchParams.get("code");
    if (code) {
      connectStripeRef.current({ variables: { input: { code } } });
    } else {
      replace(`/user/${viewerMemo.id}`);
    }
  }, [viewerMemo, replace]);

  if (loading) {
    return (
      <Content className="stripe">
        <Spin size="large" tip="Connection to your Stripe account..." />
      </Content>
    );
  }
  if (error) {
    return <Redirect to={`/user/${viewer.id}?stripe_error=true`} />;
  }

  if (data) {
    return <Redirect to={`/user/${viewer.id}`} />;
  }
  return null;
};
