import { FC } from "react";
import { Typography, List } from "antd";

import { User } from "lib/graphql/queries";
import { ListingCard } from "lib/components";

interface Props {
  limit: number;
  bookingsPage: number;
  userBookings: User["user"]["bookings"];
  setBookingsPage: (page: number) => void;
}

const { Paragraph, Title, Text } = Typography;

export const UserBookings: FC<Props> = ({
  limit,
  bookingsPage,
  userBookings,
  setBookingsPage,
}) => {
  const total = userBookings?.total || null;
  const result = userBookings?.result || null;

  const userBookingsList = userBookings ? (
    <List
      dataSource={result || undefined}
      grid={{ gutter: 8, xs: 1, sm: 2, lg: 4 }}
      locale={{ emptyText: "User doesn't have any listings yet!" }}
      pagination={{
        position: "top",
        showLessItems: true,
        current: bookingsPage,
        defaultPageSize: limit,
        hideOnSinglePage: true,
        total: total || undefined,
        onChange: (page: number) => setBookingsPage(page),
      }}
      renderItem={(booking) => {
        const bookingHistory = (
          <div className="user-bookings__booking-history">
            <div>
              Check in: <Text strong>{booking.checkIn}</Text>
            </div>
            <div>
              Check out: <Text strong>{booking.checkOut}</Text>
            </div>
          </div>
        );

        return (
          <List.Item>
            {bookingHistory}
            <ListingCard listing={booking.listing} />
          </List.Item>
        );
      }}
    />
  ) : null;

  return userBookingsList ? (
    <div className="user-bookings">
      <Title level={4} className="user-bookings__title">
        Bookings
      </Title>
      <Paragraph className="user-bookings__description">
        This section highlights the bookings you've made, and the
        check-in/check-out dates associated with said bookings.
      </Paragraph>
      {userBookingsList}
    </div>
  ) : null;
};
