import { FC, useState } from "react";
import { Col, Row, Layout } from "antd";
import { useQuery } from "@apollo/client";
import { useRouteMatch } from "react-router-dom";

import { Viewer } from "lib/types";
import { PageSkeleton, ErrorBanner } from "lib/components";
import { UserProfile, UserBookings, UserListings } from "./components";
import { USER, User as UserData, UserVariables } from "lib/graphql/queries";

interface RouteParams {
  id: string;
}

interface Props {
  viewer: Viewer;
  setViewer(viewer: Viewer): void;
}

const PAGE_LIMIT = 4;
const { Content } = Layout;

export const User: FC<Props> = ({ viewer, setViewer }) => {
  const [bookingsPage, setBookingsPage] = useState(1);
  const [listingsPage, setListingsPage] = useState(1);
  const { id } = useRouteMatch<RouteParams>().params;

  const { data, loading, error, refetch } = useQuery<UserData, UserVariables>(
    USER,
    {
      variables: { id, limit: PAGE_LIMIT, bookingsPage, listingsPage },
    }
  );

  const handleUserRefetch = async () => {
    await refetch();
  };

  const stripeError = new URL(window.location.href).searchParams.get(
    "stripe_error"
  );
  const stripeErrorBanner = stripeError ? (
    <ErrorBanner description="We had an issue connecting with Stripe. Please try again soon." />
  ) : null;

  if (loading) {
    return (
      <Content className="user">
        <PageSkeleton />
      </Content>
    );
  }

  if (error) {
    return (
      <Content className="user">
        <ErrorBanner description="This user may not exist or we've encountered an error. Please try again soon." />
        <PageSkeleton />
      </Content>
    );
  }

  const user = data?.user || null;
  const viewerIsUser = viewer.id === id;

  const userListings = user?.listings || null;
  const userBookings = user?.bookings || null;

  const userListingsElement = userListings ? (
    <UserListings
      limit={PAGE_LIMIT}
      userListings={userListings}
      listingsPage={listingsPage}
      setListingsPage={setListingsPage}
    />
  ) : null;

  const userBookingsElement = userListings ? (
    <UserBookings
      limit={PAGE_LIMIT}
      userBookings={userBookings}
      bookingsPage={bookingsPage}
      setBookingsPage={setBookingsPage}
    />
  ) : null;

  const userProfileElement = user ? (
    <UserProfile
      user={user}
      viewer={viewer}
      setViewer={setViewer}
      viewerIsUser={viewerIsUser}
      handleUserRefetch={handleUserRefetch}
    />
  ) : null;

  return (
    <Content className="user">
      {stripeErrorBanner}
      <Row gutter={12} typeof="flex" justify="space-between">
        <Col xs={24}>{userProfileElement}</Col>
        <Col xs={24}>
          {userListingsElement}
          {userBookingsElement}
        </Col>
      </Row>
    </Content>
  );
};
