import { gql } from "@apollo/client";

export const LISTING = gql`
  query Listing($id: ID!, $bookingsPage: Int!, $limit: Int!) {
    listing(id: $id) {
      id
      city
      type
      price
      title
      image
      address
      description
      numOfGuests
      bookingsIndex
      host {
        id
        name
        avatar
        hasWallet
      }
      bookings(limit: $limit, page: $bookingsPage) {
        total
        result {
          id
          checkIn
          checkOut
          tenant {
            id
            name
            avatar
          }
        }
      }
    }
  }
`;

export * from "./__generated__/Listing";
