import { FC, useState } from "react";
import { Moment } from "moment";
import { Layout, Col, Row } from "antd";
import { useQuery } from "@apollo/client";
import { useRouteMatch } from "react-router-dom";

import {
  ListingDetails,
  ListingBookings,
  ListingCreateBooking,
  ListingCreateBookingModal,
} from "./components";
import {
  LISTING,
  ListingVariables,
  Listing as ListingData,
} from "lib/graphql/queries/Listing";
import { Viewer } from "lib/types";
import { PageSkeleton, ErrorBanner } from "lib/components";

interface RouteParams {
  id: string;
}

interface Props {
  viewer: Viewer;
}

const PAGE_LIMIT = 3;
const { Content } = Layout;

export const Listing: FC<Props> = ({ viewer }) => {
  const { id } = useRouteMatch<RouteParams>().params;
  const [bookingsPage, setBookingsPage] = useState(1);
  const [modalVisible, setModalVisible] = useState(false);
  const [checkInDate, setCheckInDate] = useState<null | Moment>(null);
  const [checkOutDate, setCheckOutDate] = useState<null | Moment>(null);

  const { data, loading, error, refetch } = useQuery<
    ListingData,
    ListingVariables
  >(LISTING, { variables: { id, limit: PAGE_LIMIT, bookingsPage } });

  const clearBookingData = () => {
    setModalVisible(false);
    setCheckInDate(null);
    setCheckOutDate(null);
  };

  const handleListingRefetch = async () => {
    refetch();
  };

  if (loading) {
    return (
      <Content className="listings">
        <PageSkeleton />
      </Content>
    );
  }

  if (error) {
    return (
      <Content className="listings">
        <PageSkeleton />
        <ErrorBanner description="This listing may not exist or ew've encountered an error. Please try again soon!" />
      </Content>
    );
  }

  const listing = data?.listing || null;
  const listingBookings = data?.listing.bookings || null;

  const listingDetailsElement = listing ? (
    <ListingDetails listing={listing} />
  ) : null;

  const listingBookingsElement = listingBookings ? (
    <ListingBookings
      limit={PAGE_LIMIT}
      bookingsPage={bookingsPage}
      setBookingsPage={setBookingsPage}
      listingBookings={listingBookings}
    />
  ) : null;

  const listingCrateBookingElement = listing ? (
    <ListingCreateBooking
      viewer={viewer}
      host={listing.host}
      price={listing.price}
      checkInDate={checkInDate}
      checkOutDate={checkOutDate}
      setCheckInDate={setCheckInDate}
      setCheckOutDate={setCheckOutDate}
      setModalVisible={setModalVisible}
      bookingsIndex={listing.bookingsIndex}
    />
  ) : null;

  const listingCreateBookingModalElement =
    listing?.price && checkInDate && checkOutDate ? (
      <ListingCreateBookingModal
        id={listing.id}
        price={listing?.price}
        checkInDate={checkInDate}
        checkOutDate={checkOutDate}
        modalVisible={modalVisible}
        setModalVisible={setModalVisible}
        clearBookingData={clearBookingData}
        handleListingRefetch={handleListingRefetch}
      />
    ) : null;

  return (
    <Content className="listings">
      <Row gutter={24} typeof="flex" justify="space-between">
        <Col xs={24} lg={14}>
          {listingDetailsElement}
          {listingBookingsElement}
        </Col>
        <Col xs={24} lg={10}>
          {listingCrateBookingElement}
        </Col>
      </Row>
      {listingCreateBookingModalElement}
    </Content>
  );
};
