import { FC } from "react";
import { Link } from "react-router-dom";
import { Button, Menu, Avatar } from "antd";
import { useMutation } from "@apollo/client";
import { LOG_OUT, LogOut as LogOutData } from "lib/graphql/mutations";
import { HomeOutlined, UserOutlined, LogoutOutlined } from "@ant-design/icons";

import { Viewer } from "lib/types";
import { displaySuccessNotification, displayErrorMessage } from "lib/utils";

const { Item, SubMenu } = Menu;

interface Props {
  viewer: Viewer;
  setViewer: (viewer: Viewer) => void;
}

export const MenuItems: FC<Props> = ({ viewer, setViewer }) => {
  const { id, avatar } = viewer;

  const [logOut] = useMutation<LogOutData>(LOG_OUT, {
    onCompleted: ({ logOut }) => {
      setViewer(logOut);
      sessionStorage.removeItem("token");
      displaySuccessNotification("You have successfully logged out!");
    },
    onError: () =>
      displayErrorMessage(
        "Sorry! We weren't able to log you out. Please try again later!"
      ),
  });

  const handleLogOut = () => {
    logOut();
  };

  const subMenuLogin = id ? (
    <SubMenu title={<Avatar src={avatar} />}>
      <Item key="/user">
        <Link to={`/user/${id}`}>
          <UserOutlined />
          Profile
        </Link>
      </Item>
      <Item key="/logout">
        <div onClick={handleLogOut}>
          <LogoutOutlined />
          Log Out
        </div>
      </Item>
    </SubMenu>
  ) : (
    <Item>
      <Link to="/login">
        <Button type="primary">Sign In</Button>
      </Link>
    </Item>
  );

  return (
    <Menu mode="horizontal" selectable={false} className="menu">
      <Item key="/host">
        <Link to="/host">
          <HomeOutlined /> Host
        </Link>
      </Item>
      {subMenuLogin}
    </Menu>
  );
};
