import { FC } from "react";
import { Card, List, Skeleton } from "antd";

import listingLoadingCardCover from "sections/Listings/assets/listing-loading-card-cover.jpg";

export const ListingsSkeleton: FC = () => {
  const emptyData = new Array(8).fill({});
  return (
    <div>
      <Skeleton paragraph={{ rows: 1 }} />
      <List
        grid={{ gutter: 8, xs: 1, sm: 2, md: 3, lg: 3, xl: 4 }}
        dataSource={emptyData}
        renderItem={() => (
          <List.Item>
            <Card
              loading
              className="listings-skeleton__card"
              cover={
                <div
                  className="listings-skeleton__card-cover-img"
                  style={{ backgroundColor: `url(${listingLoadingCardCover})` }}
                />
              }
            />
          </List.Item>
        )}
      />
    </div>
  );
};
