import { FC } from "react";
import { Pagination } from "antd";

interface Props {
  page: number;
  limit: number;
  total: number;
  setPage(page: number): void;
}

export const ListingsPagination: FC<Props> = ({
  page,
  total,
  limit,
  setPage,
}) => {
  return (
    <Pagination
      total={total}
      showLessItems
      current={page}
      hideOnSinglePage
      onChange={setPage}
      defaultPageSize={limit}
      className="listings-pagination"
    />
  );
};
