import { useQuery } from "@apollo/client";
import { Layout, List, Typography, Affix } from "antd";
import { useRouteMatch, Link } from "react-router-dom";
import { FC, useState, useEffect, useRef } from "react";

import {
  ListingsFilter,
  ListingsSkeleton,
  ListingsPagination,
} from "./components";
import {
  LISTINGS,
  ListingsVariables,
  Listings as ListingsData,
} from "lib/graphql/queries";
import { ListingFilter } from "lib/graphql/globalTypes";
import { ListingCard, ErrorBanner } from "lib/components";

const PAGE_LIMIT = 8;
const { Content } = Layout;
const { PRICE_LOW_TO_HIGH } = ListingFilter;
const { Title, Paragraph, Text } = Typography;

interface MatchParams {
  location: string;
}

export const Listings: FC = () => {
  const [page, setPage] = useState(1);
  const [filter, setFilter] = useState(PRICE_LOW_TO_HIGH);
  const { location } = useRouteMatch<MatchParams>().params;

  const locationRef = useRef(location);

  const { data, loading, error } = useQuery<ListingsData, ListingsVariables>(
    LISTINGS,
    {
      variables: {
        page,
        filter,
        location,
        limit: PAGE_LIMIT,
      },
      skip: locationRef.current !== location && page !== 1,
    }
  );

  useEffect(() => {
    setPage(1);
    locationRef.current = location;
  }, [location]);

  if (loading) {
    return (
      <Content className="listings">
        <ListingsSkeleton />
      </Content>
    );
  }

  if (error) {
    return (
      <Content className="listings">
        <ErrorBanner
          description="
            We either couldn't find anything matching your search or have encountered an error.
            If you're searching for a unique location, try searching again with more common keywords.
          "
        />
        <ListingsSkeleton />
      </Content>
    );
  }

  const listings = data?.listings || null;
  const listingsRegion = data?.listings.region || null;

  const listingsSectionElement =
    listings && listings.result.length ? (
      <div>
        <Affix offsetTop={64}>
          <div>
            <ListingsPagination
              page={page}
              setPage={setPage}
              limit={PAGE_LIMIT}
              total={listings.total}
            />
            <ListingsFilter filter={filter} setFilter={setFilter} />
          </div>
        </Affix>
        <List
          dataSource={listings.result}
          grid={{ gutter: 8, xs: 1, sm: 2, md: 3, lg: 3, xl: 4 }}
          renderItem={(listing) => (
            <List.Item>
              <ListingCard listing={listing} />
            </List.Item>
          )}
        />
      </div>
    ) : (
      <div>
        <Paragraph>
          It appears that no listings have yet been created for{" "}
          <Text mark>"{listingsRegion}"</Text>
        </Paragraph>
        <Paragraph>
          Be the first person to create a{" "}
          <Link to="/host">listing in this area</Link>!
        </Paragraph>
      </div>
    );

  const listingsRegionElement = listingsRegion ? (
    <Title level={3} className="listings__title">
      Result for "{listingsRegion}"
    </Title>
  ) : null;

  return (
    <Content className="listings">
      {listingsRegionElement}
      {listingsSectionElement}
    </Content>
  );
};
