import { FC } from "react";
import { useQuery } from "@apollo/client";
import { Layout, Typography, Col, Row } from "antd";
import { Link, useHistory } from "react-router-dom";

import {
  LISTINGS,
  ListingsVariables,
  Listings as ListingsData,
} from "lib/graphql/queries/Listings";
import { displayErrorMessage } from "lib/utils";
import { ListingFilter } from "lib/graphql/globalTypes";
import { HomeHero, HomeListings, HomeListingsSkeleton } from "./components";

import cancunImage from "./assets/cancun.jpg";
import mapBackground from "./assets/map-background.jpg";
import sanFranciscoImage from "./assets/san-fransisco.jpg";

const { Content } = Layout;
const { Title, Paragraph } = Typography;

const PAGE_LIMIT = 4;
const PAGE_NUMBER = 1;

export const Home: FC = () => {
  const { push } = useHistory();

  const { data, loading } = useQuery<ListingsData, ListingsVariables>(
    LISTINGS,
    {
      variables: {
        limit: PAGE_LIMIT,
        page: PAGE_NUMBER,
        filter: ListingFilter.PRICE_HIGH_TO_LOW,
      },
    }
  );

  const onSearch = (value: string) =>
    value.trim()
      ? push(`/listings/${value.trim()}`)
      : displayErrorMessage("Please enter a valid search value!");

  const renderListingsSection = () => {
    if (loading) {
      return <HomeListingsSkeleton />;
    }

    if (data) {
      const listings = data.listings.result;
      return <HomeListings title="Premium Listings" listings={listings} />;
    }

    return null;
  };
  return (
    <Content
      className="home"
      style={{ backgroundImage: `url(${mapBackground})` }}
    >
      <HomeHero onSearch={onSearch} />
      <div className="home__cta-section">
        <Title level={2} className="home__cta-section-title">
          Your guide for all things rental
        </Title>
        <Paragraph>
          Helping you make the best decisions in renting your last minute
          locations.
        </Paragraph>
        <Link
          to="/listings/unites%20states"
          className="ant-btn ant-btn-primary ant-btn-lg home__cta-section-button"
        >
          Popular listings in united states
        </Link>
      </div>
      {renderListingsSection()}
      <div className="home__listings">
        <Title level={4} className="home__listings-title">
          Listings of any kind
        </Title>
        <Row gutter={12}>
          <Col xs={24} sm={12}>
            <Link to="/listins/san%20francisco">
              <div className="home_listings-img-cover">
                <img
                  alt="San Francisco"
                  src={sanFranciscoImage}
                  className="home__listings-img"
                />
              </div>
            </Link>
          </Col>
          <Col xs={24} sm={12}>
            <Link to="/listings/cancun">
              <div className="home_listings-img-cover">
                <img
                  alt="Cancun"
                  src={cancunImage}
                  className="home__listings-img"
                />
              </div>
            </Link>
          </Col>
        </Row>
      </div>
    </Content>
  );
};
