import React, { FC, useState, useEffect, useRef } from "react";

import { render } from "react-dom";
import { Layout, Affix, Spin } from "antd";
import { setContext } from "@apollo/client/link/context";
import { useMutation, createHttpLink } from "@apollo/client";
import { StripeProvider, Elements } from "react-stripe-elements";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { ApolloClient, InMemoryCache, ApolloProvider } from "@apollo/client";

import {
  User,
  Home,
  Host,
  Login,
  Stripe,
  Listing,
  Listings,
  NotFound,
  AppHeader,
} from "sections";
import {
  LOG_IN,
  LogInVariables,
  LogIn as LogInData,
} from "lib/graphql/mutations";
import { Viewer } from "lib/types";
import { AppHeaderSkeleton, ErrorBanner } from "lib/components";

import "./styles/index.css";

const httpLink = createHttpLink({ uri: "/api" });

const setCSRFHeader = setContext((_req, prevContext) => {
  const token = sessionStorage.getItem("token") || "";
  return {
    ...prevContext,
    headers: { "X-CSRF-TOKEN": token },
  };
});

const client = new ApolloClient({
  credentials: "include",
  cache: new InMemoryCache(),
  link: setCSRFHeader.concat(httpLink),
});

const initialViewer: Viewer = {
  id: null,
  token: null,
  avatar: null,
  hasWallet: null,
  didRequest: false,
};

const App: FC = () => {
  const [viewer, setViewer] = useState(initialViewer);
  const [logIn, { error }] = useMutation<LogInData, LogInVariables>(LOG_IN, {
    onCompleted: ({ logIn }) => {
      setViewer(logIn);
      if (logIn.token) {
        sessionStorage.setItem("token", logIn.token);
      } else {
        sessionStorage.removeItem("token");
      }
    },
  });

  const logInRef = useRef(logIn);

  useEffect(() => {
    logInRef.current();
  }, []);

  if (!viewer.didRequest && !error) {
    return (
      <Layout className="app-skeleton">
        <AppHeaderSkeleton />
        <div className="app-skeleton__spin-section">
          <Spin size="large" tip="Launching Tinyhouse" />
        </div>
      </Layout>
    );
  }

  const logInErrorBannerElement = error ? (
    <ErrorBanner description="We weren't able to verify if you were logged in. Please try again later!" />
  ) : null;

  const apiKey = process.env.REACT_APP_S_PUBLICHED_KEY;

  return (
    <StripeProvider apiKey={apiKey}>
      <Router>
        <Layout id="app">
          {logInErrorBannerElement}
          <Affix offsetTop={0}>
            <AppHeader viewer={viewer} setViewer={setViewer} />
          </Affix>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/host">
              <Host viewer={viewer} />
            </Route>
            <Route exact path="/login">
              <Login setViewer={setViewer} />
            </Route>
            <Route exact path="/stripe">
              <Stripe viewer={viewer} setViewer={setViewer} />
            </Route>
            <Route exact path="/user/:id">
              <User setViewer={setViewer} viewer={viewer} />
            </Route>
            <Route exact path="/listing/:id">
              <Elements>
                <Listing viewer={viewer} />
              </Elements>
            </Route>
            <Route exact path="/listings/:location?" component={Listings} />
            <Route component={NotFound} />
          </Switch>
        </Layout>
      </Router>
    </StripeProvider>
  );
};

render(
  <ApolloProvider client={client}>
    <App />
  </ApolloProvider>,
  document.getElementById("root")
);

declare global {
  namespace NodeJS {
    interface ProcessEnv {
      REACT_APP_S_CLIENT_ID: string;
      REACT_APP_S_OAUTH_URL: string;
      REACT_APP_S_PUBLICHED_KEY: string;
    }
  }
}
